import { IAssetList } from "@stechquick/algae/lib/quick/IPlateauUI";
import { createRenderer, init } from "@stechquick/websdk";

const quiRootParent = document.createElement("div");
const quiRoot = document.createElement("div");
quiRootParent.appendChild(quiRoot);

declare var window: Window & { websdk: { createRenderer: typeof createRenderer, init: typeof init }, quiProms: { web: { p: Promise<any> }, iam: { p: Promise<any> }, asset: { p: Promise<any> } } };


var PlateauUIOptions = {
    corsEnabled: true,
    iam: window.quiProms.iam.p,
    settings: window.quiProms.web.p,
    asset: window.quiProms.asset.p
}

window.websdk.init(PlateauUIOptions,"qui").then(async () => {
    var renderer = await window.websdk.createRenderer({ mountPoint: quiRoot });
    renderer.open();
    document.body.appendChild(quiRootParent);
});

